@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: 'Raleway', sans-serif;
    font-family: 'Black Han Sans', sans-serif;
}
body,html{margin: 0; padding: 0;}
.mName{
    font-size: 6rem;
    text-align: center;
    margin: 15% auto;
}
.app{
    width: 30%;
    font-size: 1.5rem;
    margin: 2% 2%;
    position: fixed;
    left: 0; top: 0;
    display: flex;
    flex-direction: column;
    
}
.app > a{
    display: block;
    color: #000;
    text-decoration: none;
    margin: 3% 1%;
    font-family: 'Raleway', sans-serif;
}
.app > a:hover{
    color: rgb(37, 59, 185);
    font-weight: bold;
}

/* --------------weather-------------------------------------------------- */
.sec{
    width: 90%;
    margin: 3% auto;
    text-align: center;
    font-family: 'Raleway', sans-serif;
}
.sec_top{
    position: relative;
    width: 70%;
    margin: 0 auto;
}
.sec_top > h2{
    width: 12%;
    margin: 0 auto;
    font-family: 'Raleway', sans-serif;
}
.sec_top > h2 > img{width: 100%;}
.sec_top > p{
    margin: 0 auto;
    width: 20%;
    position: absolute;
    left: 0; bottom: 0;
    font-family: 'Raleway', sans-serif;
}
.sec_top > div{
    text-align: right;
    margin: 1% auto;
    font-family: 'Raleway', sans-serif;
}
#station{
    margin: 0 3%;
    padding: 0 2%;
    cursor: pointer;
    font-family: 'Raleway', sans-serif;
}
.sec_top > div > select > option{
    font-family: 'Raleway', sans-serif;
    
}
.sec_top > div > button{
    background-color: rgb(37, 59, 185);
    border: 2px solid rgb(37, 59, 185);
    cursor: pointer;
    padding: 0.1% 2%;
    color: #fff;
    font-family: 'Raleway', sans-serif;
}
.tab{
    margin: 0 auto;
    width: 70%;
    border: none;
}
.tab > thead{
    background-color: royalblue;
    color: #fff;
}
.tab > thead th{
    padding: 0 0.5%;
    font-family: 'Raleway', sans-serif;
}
.tab > tbody{
    background-color: rgba(255, 245, 238, 0.6);
}
.tab > tbody td{
    font-family: 'Raleway', sans-serif;
}
.back{
    z-index: -1;
    position: absolute;
    left: 0; top: -3%;
    opacity: 0.3;
    width: 100%;
}
.back > img{width: 100%;}

/* ----------------------------------------MBTI------------------ */
.mbti{
    display: flex;
    justify-content: center;
    align-items: center;
}
.mTest{
    background-color: rgba(71, 49, 212, 0.8);
    width: 30%;
    margin: 3% auto 3% 15%;
    text-align: center;
    padding: 2%;
}
.mTest > article{
    color: #fff;
}
.mTest > article > h3{
    font-size: 3rem;
    font-weight: normal;
}
.mTest > article > p{
    font-size: 1.5rem;
    background-color: #fff;
    color: rgba(71, 49, 212, 0.8);
    width: 50%; 
    margin: 5% auto;
    padding: 2%;
    border-radius: 20px;
}
.mTest > article::after{
    content: '';
    display: block;
    width: 70%; height: 1px;
    background-color: #fff;
    margin: 2% auto;
}

.m1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 3%;
}
.m1 a{
    display: block;
    width: 40%;
    background-color: transparent ;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 5;
}
.m1 a > code{
    display: block;
    font-size: 3.5rem;
    font-family: 'Black Han Sans', sans-serif;
    z-index: -1;
}
.m1 a > p{
    margin: 0 auto;
    font-family: 'Raleway', sans-serif;
    font-size: 1.2rem;
    font-weight: bold;
    color: rgb(199, 195, 224);
    z-index: -1;
}
.mClick span{
    display: block;
    color: rgba(162, 152, 221, 0.8);
    font-size: 2rem;
}
.left{
    color: rgb(89, 255, 238);
}
.right{
    color: yellow;
}
.mbti-text{

}
.mbti-text > .result{
    border: 1px solid #fff;
    background-color: #fff;
    width: 30%; height: 5vh;
    padding-top: 2%;
    margin: 0 auto;
    position: relative;
}
.mbti-text > .result > span{
    font-size: 2rem;
}
.mbti-text > .result .reInput{
    cursor: pointer;
    display: block;
    position: absolute;
    right: -40%; bottom: 0;
    padding: 1% 3%;
    color: rgb(110, 110, 110);
}
.mbti-text > .okInput{
    cursor: pointer;
    display: block;
    margin: 2% auto;
    text-align: center;
    padding: 1% 5%;
    font-size: 1.2rem;
    color: rgb(110, 110, 110);
}


/* ---------------------------------------------------------- */

.allResult{
    width: 20%; 
    text-align: center;
    margin: 0 auto;
    background-color: rgba(71, 49, 212, 0.2);
    padding: 3%;
}
.reTitle{}
.reTitle > h2{
    font-weight: normal;
    font-size: 2rem;
    margin: 0 auto;
}
.reTitle > p{
    color: rgb(110, 110, 110);
}
.reTitle > span{
    font-size: 2.5rem;
    display: block;
    margin: 15% 0 0;
}

.reTextAll{
    background-color: #fff;
    padding: 3%;
    margin-top: 3%;
}
.reText{}
.reText > .reMbti{
    width: 100%; height: 5vh;
}
.reText > .reMbti > h3{
    font-size: 2rem;
    font-weight: normal;
}
.reText > .reSpan{
    width: 100%; height: 5vh;
    margin: 2% 0;
}
.reText > .reSpan > span{
    font-size: 1.5rem;
    font-family: 'Raleway', sans-serif;
}
.reText > .reP{
    width: 100%; height: 30vh;
    overflow-y: scroll;
}
.reText > .reP > p{
    font-family: 'Raleway', sans-serif;
    line-height: 2rem;
    padding: 0 5%;
}
